@mixin containers {
    min-height: 100vh;
    max-height: 100vh;
    margin: 0;
}

.total_container {
@include containers;
}

.home_container {
@include containers;
background: #252934;
background-size: cover;

}

.proyects-container {
background: var(--blue);
}

.contact-container {
background: #2e4d70;
margin: 0;
}

.footer-container {
@include containers; 
min-height: 210px;
max-height: 210px;
background: #1B242F;
}

