.main-about-container {
    background: var(--darkblue);
    font-family: "Nunito";
    display: flex;
    flex-direction: column;
}
.about-body{
    margin: 40px 0px 0px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.about-body-picture {
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 90%;
    margin: 20px auto auto auto;  
}
.profile-image-container {
margin: 0px 50px 0px 50px;
 
}
.profile-paragraph-container {
width: 35%;

    p{
    color: #979797;
    font-size: 18px;
    font-family: "Montserrat";
    text-align: justify;
    }
    
    h3{
        color:  var(--white);

        span {
            color: var(--blue);
        }
    }
   
}
.about-body-skills{
    border-radius: 3px;
    margin: 40px 0px 10px 0px;
    display: flex;
    flex-direction: column;
    align-items: flex;
    justify-content: flex-end;
    width: 100%;
    background: var(--blue);

    h1{
    margin: 5px;
    width: 100%;
    text-align: center;
    }
    }
    
.skills {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 60px 0px;

    .front-skills {   
        display: flex;
        flex-direction: column;  
        width: 40%;
        align-items: center;
        justify-content: center;

        h1 {
            margin-bottom: 40px;
            background: var(--green);
            color: var(--white);
            width: 50%;
            border-radius: 3pxs;
        }

        h2 {
            display: flex;
            justify-content: flex-start;
            background: #294565;
            text-align: center;
            width: 90%;
            margin-top: 5px;
            color: var(--white);

            &:hover {
                span {
                    width: 100%;
                }
            }

            span {
                transition: .33s;
                font-size: 25px;
                text-align: center;
                width: 35%;
                background: #20364f;
            }
        }
    }

    .back-skills {
        display: flex;
        flex-direction: column; 
        width: 40%;
        align-items: center;
        justify-content: center;

        h1 {
            border-radius: 3px;
            margin-bottom: 40px;
            background: var(--green);
            color: var(--white);
            width: 50%;
        }

        h2 {
            display: flex;
            justify-content: flex-start;
            background: #294565;
            text-align: center;
            width: 90%;
            margin-top: 5px;
            color: var(--white);

            &:hover {
                span {
                    width: 100%;
                }
            }

            span {
                font-size: 25px;
                text-align: center;
                width: 35%;
                transition: .33s;
                background: #20364f;

               
            }
        }
    }
}

@media screen and (max-width: 1050px) {

    .about-body-picture{
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }

    .profile-paragraph-container{
        width: 100%;
    }
}

@media screen and (max-width: 800px) {
.about-body-skills {
    margin-top: 35px;
}
.profile-image-container {
    margin: 20px;
}
}