.main-proyects-container {
    background: #0D122A;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 1100px;
}

.proyects-navbar-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.proyects-cards-container {
    width: 85%;
    min-height: 600px;
    margin-top: 60px;
}

.grid-card{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.triangle {
    color: var(--darkblue);
    box-sizing: border-box;
    width: 0;
    height: 0;
    border-top: 90px solid var(--darkblue);
    border-left: 49.2vw solid transparent; 
    border-right: 49.2vw solid transparent; 
}




